export const handleSelectOptions = ({ $city, $district }) => {
  const city = $city.options[$city.selectedIndex].textContent;
  $district.innerHTML = `<option selected disabled></option>${options[city]
    .map(district => `<option>${district}</option>`)
    .join()}`;
};
